Object.defineProperty(exports, "__esModule", { value: true });
const RenderSettingsSchema_1 = require("./RenderSettingsSchema");
const Joi = require("joi");
const index_1 = require("../../index");
const PaymentIntentionSchema_1 = require("../../api/expressCheckout/models/PaymentIntentionSchema");
const ExpressCheckoutRenderer_1 = require("../../api/expressCheckout/ExpressCheckoutRenderer");
/**
 * Peinau Checkout Button
 * @class CheckoutButton
 */
class CheckoutButton {
    /**
     * Creates an instance of CheckoutButton.
     * @param {*} settings settings for the configuration
     * @param {string|Element} selector selector of the element
     * @memberof CheckoutButton
     */
    constructor(settings, selector) {
        //Model Validation
        const setting_validation = Joi.validate(settings, RenderSettingsSchema_1.renderSettingsSchema);
        if (setting_validation.error) {
            //console.log(result.error);
            throw Error(JSON.stringify(setting_validation.error));
        }
        // Add the element directly or need to find them ??
        if (selector instanceof Element) {
            this.element = selector;
        }
        else {
            this.element = index_1.utils.dom.findBySelector(selector);
        }
        this.settings = setting_validation.value;
        this.payment_method = this.element.getAttribute('data-payment-method');
        this.render();
    }
    /**
     * Render the Checkout Buttons with the specified general properties
     * @memberof CheckoutButton
     */
    render() {
        this.element.innerHTML = this.payment_method;
        //Execute The callback in click Event
        index_1.utils.events.bind(this.element, 'click', (evt) => {
            //TODO: Localize
            this.element.innerHTML = 'Generando Pago...';
            this.settings
                .payment(this.payment_method)
                .then((intention) => {
                // tslint:disable-next-line:no-debugger
                debugger;
                const payment_validation = Joi.validate(intention, PaymentIntentionSchema_1.paymentIntentionSchema, { allowUnknown: true });
                if (payment_validation.error) {
                    //console.log(result.error);
                    return this.settings.onError(payment_validation.error);
                }
                this.element.innerHTML = this.payment_method;
                ExpressCheckoutRenderer_1.expressCheckoutRenderer
                    .payWithConnect(intention)
                    .then((data) => {
                    this.settings.onSuccess(data);
                }, (error) => {
                    this.settings.onError(error);
                });
            })
                .catch((error) => {
                //console.log(error);
                this.settings.onError(error);
            });
        });
    }
}
exports.CheckoutButton = CheckoutButton;



